"use client";

import * as React from "react";
import { Package, TrendingUp, Star, Candy as CandyIcon } from "lucide-react";
import {
  Card,
  CardHeader,
  CardTitle,
  CardDescription,
  CardAction,
  CardFooter,
} from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import type { Candy } from "@/lib/candy";

interface CandyInsightsProps {
  candies: Candy[];
}

export function CandyInsights({ candies }: CandyInsightsProps) {
  // Calculate statistics
  const totalUniqueTypes = candies.length;
  const totalPieces = candies.reduce((sum, candy) => sum + candy.count, 0);
  const averagePieces = totalUniqueTypes > 0 ? Math.round(totalPieces / totalUniqueTypes * 10) / 10 : 0;
  
  // Find top candy
  const topCandy = candies.length > 0 
    ? candies.reduce((max, candy) => candy.count > max.count ? candy : max, candies[0])
    : null;

  const stats = [
    {
      label: "Total Types",
      value: totalUniqueTypes,
      icon: Package,
      description: "Unique candy varieties in your collection",
    },
    {
      label: "Average Pieces",
      value: averagePieces,
      icon: TrendingUp,
      description: "Average pieces per candy type",
    },
    {
      label: "Top Candy",
      value: topCandy ? topCandy.candy_name : "N/A",
      count: topCandy ? topCandy.count : null,
      icon: Star,
      description: topCandy ? `${topCandy.count} pieces collected` : "No candy yet",
    },
    {
      label: "Total Pieces",
      value: totalPieces,
      icon: CandyIcon,
      description: "Total pieces across all types",
    },
  ];

  // Format number consistently to avoid hydration mismatch
  const formatNumber = (value: number | string): string => {
    if (typeof value === "string") return value;
    // Use en-US locale to ensure consistent formatting between server and client
    if (Number.isInteger(value)) {
      return value.toLocaleString("en-US");
    }
    // For decimals, show up to 1 decimal place
    return value.toLocaleString("en-US", { 
      minimumFractionDigits: 0, 
      maximumFractionDigits: 1 
    });
  };

  return (
    <div className="grid grid-cols-1 gap-4 px-4 sm:grid-cols-2 lg:grid-cols-4 lg:px-6">
      {stats.map((stat) => {
        const Icon = stat.icon;
        const displayValue = stat.value === "N/A" ? "N/A" : formatNumber(stat.value);
        
        return (
          <Card key={stat.label} className="@container/card from-primary/5 to-card dark:bg-card bg-gradient-to-t shadow-xs">
            <CardHeader>
              <CardDescription>{stat.label}</CardDescription>
              <CardTitle className="text-2xl font-semibold tabular-nums @[250px]/card:text-3xl">
                {stat.value === "N/A" ? (
                  <Badge variant="outline">{displayValue}</Badge>
                ) : (
                  <>
                    {displayValue}
                    {stat.count !== null && (
                      <span className="text-lg text-muted-foreground ml-2">
                        ({stat.count} pieces)
                      </span>
                    )}
                  </>
                )}
              </CardTitle>
            </CardHeader>
            <CardFooter className="flex-col items-start gap-1.5 text-sm">
              <div className="line-clamp-1 flex gap-2 font-medium">
                {stat.description} <Icon className="size-4" />
              </div>
            </CardFooter>
          </Card>
        );
      })}
    </div>
  );
}

