"use client";

import * as React from "react";
import { Bar, BarChart, CartesianGrid, XAxis, YAxis } from "recharts";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  ChartConfig,
  ChartContainer,
  ChartTooltip,
  ChartTooltipContent,
} from "@/components/ui/chart";
import type { Candy } from "@/lib/candy";

interface CandyChartProps {
  candies: Candy[];
}

type ChartDataPoint = {
  name: string;
  amount: number;
};

const CHART_HEIGHT = 300;
const EMPTY_STATE_HEIGHT = 250;
const CHART_MARGIN = { left: 20, right: 20, top: 20 };

const chartConfig = {
  amount: {
    label: "Amount",
    color: "hsl(var(--chart-1))",
  },
} satisfies ChartConfig;

const CARD_TITLE = "Candy Distribution";
const CARD_DESCRIPTION = "Visual breakdown of your candy collection";
const EMPTY_STATE_MESSAGE = "No candy data to display";

// Truncate long names for display
function truncateName(name: string, maxLength: number = 12): string {
  if (name.length <= maxLength) return name;
  return name.slice(0, maxLength - 3) + "...";
}

export function CandyChart({ candies }: CandyChartProps) {
  // Transform candy data for the chart
  const chartData = React.useMemo<ChartDataPoint[]>(() => {
    return candies
      .filter((candy) => candy.count > 0) // Only show candies with counts > 0
      .map((candy) => ({
        name: candy.candy_name,
        amount: candy.count,
      }))
      .sort((a, b) => b.amount - a.amount); // Sort by amount descending
  }, [candies]);

  // Calculate dynamic bottom margin based on longest name
  const maxNameLength = React.useMemo(() => {
    if (chartData.length === 0) return 0;
    return Math.max(...chartData.map((item) => item.name.length));
  }, [chartData]);

  // Adjust margin based on name length and rotation
  // Longer names need more bottom space when rotated
  const bottomMargin = React.useMemo(() => {
    return maxNameLength > 15 ? 80 : maxNameLength > 10 ? 70 : 60;
  }, [maxNameLength]);

  // Memoize tooltip formatter to avoid recreating on every render
  const tooltipContent = React.useCallback(
    ({ active, payload }: Parameters<NonNullable<React.ComponentProps<typeof ChartTooltip>["content"]>>[0]) => {
      if (!active || !payload?.[0]) return null;
      const data = payload[0].payload as ChartDataPoint;
      return (
        <ChartTooltipContent>
          <div className="space-y-1">
            <p className="font-medium">{data.name}</p>
            <p className="text-sm text-muted-foreground">
              Amount: {payload[0].value}
            </p>
          </div>
        </ChartTooltipContent>
      );
    },
    []
  );

  // Memoize tick formatter to avoid recreating on every render
  const tickFormatter = React.useCallback(
    (value: string) => {
      // Truncate based on whether we have many items
      // If many items, truncate more aggressively
      const maxLen = chartData.length > 10 ? 10 : 12;
      return truncateName(value, maxLen);
    },
    [chartData.length]
  );

  if (chartData.length === 0) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>{CARD_TITLE}</CardTitle>
          <CardDescription>{CARD_DESCRIPTION}</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex items-center justify-center h-[250px] text-muted-foreground">
            {EMPTY_STATE_MESSAGE}
          </div>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>{CARD_TITLE}</CardTitle>
        <CardDescription>{CARD_DESCRIPTION}</CardDescription>
      </CardHeader>
      <CardContent>
        <ChartContainer config={chartConfig} className="h-[300px] w-full">
          <BarChart
            data={chartData}
            margin={{ ...CHART_MARGIN, bottom: bottomMargin }}
          >
            <CartesianGrid strokeDasharray="3 3" vertical={false} />
            <XAxis
              dataKey="name"
              angle={-45}
              textAnchor="end"
              height={bottomMargin}
              tick={{ fontSize: 11 }}
              tickLine={false}
              axisLine={false}
              interval={0}
              tickFormatter={tickFormatter}
            />
            <YAxis
              tick={{ fontSize: 12 }}
              tickLine={false}
              axisLine={false}
            />
            <ChartTooltip content={tooltipContent} />
            <Bar
              dataKey="amount"
              fill="var(--color-amount)"
              radius={[4, 4, 0, 0]}
            />
          </BarChart>
        </ChartContainer>
      </CardContent>
    </Card>
  );
}

